.item-powerpuff-card {
  position: relative;
}

.item-powerpuff-container {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.item-powerpuff-stylizer {
  -webkit-transform: scale(.2);
      -ms-transform: scale(.2);
          transform: scale(.2);
  position: absolute;
  width: calc(15em / 2);
}



